import React from 'react';
import { connect } from 'react-redux'

import AddWidgetButton from './AddWidgetButton';
import AddWidgetPopup from './AddWidgetPopup';
import UserActivityWidget from './UserActivityWidget';
import WidgetPlaceHolder from './WidgetPlaceHolder';

import * as Actions from './actions';

class Dashboard extends React.Component {
    constructor(props) {
        super(props);
        this.onMouseMove = this.onMouseMove.bind(this);
        this.onMouseUp = this.onMouseUp.bind(this);

        this.state = {
            dragTarget: null,
            dragging: false,
            popupVisible: false
        };        
    }

    addWidgetPopup () {
        if (this.state && this.state.popupVisible) {
            return <AddWidgetPopup onClose={() => this.setState({popupVisible: false})}/>
        }
        return false;
    }

    draggedWidget() {
        if (this.state.dragging && this.state.dragTarget !== null && this.props.widgets[this.state.dragTarget].widget) {            
            let left = this.state.dragLocation.x - this.state.dragOffsetX;
            let top = this.state.dragLocation.y - this.state.dragOffsetY;              
            return <UserActivityWidget id={this.state.dragTarget} style={{
                position: 'absolute',
                left,
                top,
                opacity: 0.5
            }}/>
        }
    }

    onHeaderClick(id, d) {
        this.setState({
            dragTarget: id,
            dragging: false,
            startX: d.x,
            startY: d.y,
            dragOffsetX: d.dx,
            dragOffsetY: d.dy
        })
        this.startDND();
    }

    onMouseMove(e) {
        if (!this.state.dragging) {
            let dx = e.pageX - this.state.startX;
            let dy = e.pageY - this.state.startY;
            if (Math.abs(dx) > 10 || Math.abs(dy) > 10) {
                this.setState({
                    dragging: true,
                    dragLocation: {
                        x: e.pageX,
                        y: e.pageY
                    }
                })
            }
        }        
        this.setState({
            dragLocation: {
                x: e.pageX,
                y: e.pageY
            }
        })
    }

    onMouseUp(e) {                
        if (this.state.dropTarget !== null && this.state.dropTarget !== undefined && this.state.dragTarget != null
            && this.state.dropTarget !== this.state.dragTarget
        ) {            
            this.props.moveWidget(this.state.dropTarget, this.state.dragTarget);
        }
        this.setState({
            dragTarget: null,
            dragging: false,
            dragLocation: null
        })
        this.stopDND();
    }

    startDND () {
        document.addEventListener('mousemove', this.onMouseMove)
        document.addEventListener('mouseup', this.onMouseUp)
    }

    stopDND() {
        document.removeEventListener('mousemove', this.onMouseMove)
        document.removeEventListener('mouseup', this.onMouseUp)
    }

    targetChange(widgetId, isTarget) {
        if (!isTarget && this.state.dropTarget == widgetId) {            
            this.setState({dropTarget: null})
        } else if (isTarget && this.state.dropTarget != widgetId) {            
            this.setState({dropTarget: widgetId});
        }
    }

    createWidget(widgetSettings, id) {
        if (!widgetSettings || !widgetSettings.widget) {
            return <WidgetPlaceHolder key={id}  id={id} dragndrop={this.state.dragging} dragLocation={this.state.dragLocation} 
                isTargetChanged={isTarget => this.targetChange(id, isTarget)}/>
        } 
        let {type, settings} = widgetSettings.widget;
        if (type == 'user_activity') {
            let widget = <UserActivityWidget 
                id={id}
                onHeaderClick={d=>this.onHeaderClick(id, d)}
                onDelete={() => this.props.removeWidget(id)}
                />
            return <WidgetPlaceHolder key={id} id={id} widget={widget}/>
        }
    }

    render() {
        return <div className="appDashboard">
            <div className="appDashboardHeader">
                <div className="appDashboardHeaderTitle">Team Dashboard</div>
                <AddWidgetButton onClick={() => this.setState({popupVisible: true})}/>
            </div>
            <div className="appDashboardWidgets">
                {this.props.widgets.map((x,idx) => this.createWidget(x, idx))}                
            </div>
            {this.draggedWidget()};
            {this.addWidgetPopup()}
        </div>
    }

    componentDidMount() {
        this.props.downloadData();
    }
};

export default connect(
    state => ({
        widgets: state.widgets
    }),
    dispatch => ({
        addWidget(id) {
            dispatch({type: 'ADD_WIDGET', id, widget: {type: 'user_activity'}})
        },
        removeWidget(id) {
            dispatch({type: 'REMOVE_WIDGET', id})
        },
        moveWidget(id, old_id) {
            dispatch({type: 'MOVE_WIDGET', id, old_id})
        },
        downloadData() {
            dispatch(Actions.downloadData());
        }
    })
)(Dashboard);




