import React from 'react'
import { connect } from 'react-redux'
import AddWidgetButton from './AddWidgetButton';

function AddWidgetPopup (props) {
    return <div className="awp-wrapper">
        <table className="awp-popup-background">
            <tbody>
                <tr>
                    <td>
                        <div className="addWidgetPopup">
                            <div className="awp-header">
                                <div className="awp-header-title">Add a widget</div>
                                <button className="awp-header-close" onClick={()=>props.onClose()}></button>
                            </div>
                            <div className="awp-leftpane">
                                <input placeholder="Search" className="awp-search"></input>
                                <div className="awp-widgetdirectory-btn active">Widget Directory</div>
                                <div className="awp-mywidget-btn">My Widget</div>
                            </div>
                            <div className="awp-rightpane">
                                <table className="awp-wdigetlist">
                                    <tbody>
                                        <tr>
                                            <td className="awp-widgetlist-img"><div className="awp-widgetlist-img-frame"></div></td>
                                            <td className="awp-widgetlist-description">
                                                <p className="awp-widgetlist-description-title">Users Activity</p>
                                                <p className="awp-widgetlist-description-author">By TimeDoctor</p>
                                                <p className="awp-widgetlist-description-text">Users who worked more or less their minimum hours required in daily, weekly and monthly</p>
                                            </td>
                                            <td className="awp-widgetlist-button">
                                                <AddWidgetButton onClick={()=>props.addWidget()}/>
                                            </td>
                                        </tr>
                                    </tbody>
                                </table>
                            </div>
                        </div>                        
                    </td>
                </tr>
            </tbody>
        </table>
    </div>    
}

export default connect(
    state => ({
        
    }),
    dispatch => ({
        addWidget() {
            dispatch({type: 'ADD_WIDGET', widget: {type: 'user_activity'}})
        }
    })
)(AddWidgetPopup);



