import React from 'react';

export default class WidgetPlaceHolder extends React.Component {
    isDropTarget() {
        if (this.props.widget || !this.widgetDiv) {
            return false;
        }
        let dl = this.props.dragLocation;
        if (!dl) {
            return false;
        }        
        let {left, top, right, bottom} = this.widgetDiv.getBoundingClientRect();          
        left +=  window.pageXOffset;
        right +=  window.pageXOffset;
        top += window.pageYOffset;
        bottom += window.pageYOffset;               
        return (left <= dl.x && top <= dl.y && right >= dl.x && bottom >= dl.y);
    }

    componentDidUpdate() {        
        this.props.isTargetChanged && this.props.isTargetChanged(this.isDropTarget());
    }

    render() {
        if (!this.props.widget) {
            let className = "widgetPlaceHolder";
            if (this.props.dragndrop) {
                className += " droptarget";
                if (this.isDropTarget()) {
                    className += " activedroptarget";
                }
            }
            return <div className={className} ref={x => {this.widgetDiv = x;}} >
                <table className="widgetPlaceHolder-table"><tbody><tr><td>
                <div className="widgetPlaceHolder-text">Drop Here</div>
                </td></tr></tbody></table>
            </div>
        } else {
            return this.props.widget;
        }
    }
}


