import {combineReducers} from 'redux';

function findFreeSlot(state) {
    for (let idx = 0; idx < state.length; ++idx) {
        if (!state[idx] || !state[idx].widget) {
            return idx;
        }
    }
    return null;
}

function widgets(state, action) {
    if (state == undefined) {
        state = [];
    }
    let new_state;
    switch(action.type) {
        case 'ADD_WIDGET': 
            let id = action.id || findFreeSlot(state);            
            new_state = [...state]
            if (id === null) {
                new_state.push({widget: action.widget});                
                new_state.push({widget: null});
                new_state.push({widget: null});                
            } else {
                new_state[id].widget = action.widget;
                if (new_state.length - action.id < 3) {
                    new_state.push({widget: null});
                    new_state.push({widget: null});
                    new_state.push({widget: null});
                }
            }
            return new_state;            
        case 'REMOVE_WIDGET':
            new_state = [...state]
            new_state[action.id] = {widget: null};
            return new_state
        case 'MOVE_WIDGET':
            if (action.id !== null && action.id !== undefined && action.old_id !== null && action.old_id !== undefined && action.id != action.old_id) {
                let oldWidget = state[action.id];
                if (oldWidget && oldWidget.widget) {
                    return state;
                }
                new_state = [...state]
                new_state[action.id] = state[action.old_id];
                new_state[action.old_id] = {widget: null};
                if (new_state.length - action.id < 3) {
                    new_state.push({widget: null});
                    new_state.push({widget: null});
                    new_state.push({widget: null});
                }
                return new_state
            }
            return state;
        case 'UPDATE_WIDGET_SETTINGS':
            let old_w = state[action.id];
            let w = {...old_w, settings: action.settings};
            new_state = state.slice(0, action.id).concat([w], state.slice(action.id + 1));
            return new_state;
        default:
            return state;
    }
}

function data(state, action) {
    if (state === undefined) {
        return {
            users:[]
        }
    }
    switch(action.type) {
        case 'DATA_LOADED':
            return action.data.users.map(
                x => ({
                        ...x, 
                        daily: action.data.daily[x.id],
                        weekly: action.data.weekly[x.id],
                        monthly: action.data.monthly[x.id]
                    })
            )            
        default:
            return state;
    }
}

export default combineReducers({
        widgets,
        data
});


