import { createSelector } from 'reselect';

function defaultSettings(type) {
    if (type == 'user_activity') {
        return {
            numberOfUsers: 5,
            sortAscending: false
        }
    }
    return {};
}

function normalizeSettings(type, settings) {
    if (!settings) {
        return settings;
    }
    if (type == 'user_activity') {
        return {
            numberOfUsers: settings.numberOfUsers || 5,
            sortAscending: !!settings.sortAscending 
        }
    }
    return settings;
}

export function widgetSettings(state, props) {    
    let settings = state.widgets[props.id].settings;
    let widgetType = state.widgets[props.id].widget.type
    return normalizeSettings(widgetType, settings) || defaultSettings(widgetType);
}

let data = state => state.data;

function compare(a, b) {
    if (a < b) return -1;
    if (a > b) return 1;
    return 0;
}

export let userActivity = createSelector(
    widgetSettings,
    data,
    (settings, data) => {
        let sortFunction = settings.sortAscending ? (a, b) => compare(a.weekly, b.weekly) : (a, b) => compare(b.weekly, a.weekly);
        let sortedData = [...data];
        sortedData.sort(sortFunction);
        let result = sortedData.slice(0, settings.numberOfUsers);
        return result;
    }
)


