import React from 'react';
import { connect } from 'react-redux'
import * as selectors from './selectors';

class UserActivityWidgetSettings extends React.Component {
    constructor (props) {
        super(props);
        
        this.state = {
            numberOfUsers: props.settings.numberOfUsers,
            sortAscending: props.settings.sortAscending
        }
    }

    onSave() {
        let settings = {
            numberOfUsers: this.state.numberOfUsers,
            sortAscending: this.state.sortAscending
        }
        this.props.updateSettings(settings);
        this.props.onSave(settings);
    }

    changeSort(sortAscending, checked) {        
        if (checked) {
            this.setState({sortAscending});
        }
    }

    render() {
        return <div className="uaw-settings-wrapper">
            <table className="uaw-settings-background">
            <tbody>
                <tr>
                    <td>
                        <div className="uaw-settings">
                            <div className="uaw-settings-header">
                                Top Highest percentage of Mobile Time Users
                            </div>
                            <div className="uaw-settings-form">
                                <div className="uaw-form-label">Number of Users</div>
                                <div>
                                    <select className="uaw-select" value={this.state.numberOfUsers} onChange={e => this.setState({numberOfUsers: e.target.value})}>
                                        <option value="1">1</option>
                                        <option value="2">2</option>
                                        <option value="3">3</option>
                                        <option value="4">4</option>
                                        <option value="5">5</option>
                                    </select>
                                </div>
                                <div className="uaw-form-label">Activty</div>
                                <div>
                                    <label><input type="radio" name="activity" checked={!this.state.sortAscending} onChange={e => this.changeSort(false, e.target.checked)}/>Highest</label>
                                    <label><input type="radio" name="activity" checked={this.state.sortAscending} onChange={e => this.changeSort(true, e.target.checked)}/>Lowest</label>
                                </div>
                                <div className="uaw-form-label">Time</div>
                                <div>
                                    <select className="uaw-select">
                                        <option value="1">Mobile Time</option>
                                    </select>
                                </div>
                                <div className="uaw-form-label">Date</div>
                                <div>
                                    <select className="uaw-select">
                                        <option value="1">Weekly</option>
                                    </select>
                                </div>
                            </div>
                            <div className="uaw-settings-buttons">
                                <button className="uaw-settings-cancel" onClick={()=>this.props.onCancel()}></button>
                                <button className="uaw-settings-save" onClick={()=>this.onSave()}></button>
                            </div>
                        </div>
                    </td>
                </tr>
            </tbody>
            </table>
        </div>
    }
}

function getSettings(state, id) {
    let w = state.widgets[id];
    if (!w) {
        return null;
    }
    return w.settings;
}

export default connect(
    (state, ownProps) => ({
        settings: selectors.widgetSettings(state, ownProps)
    }),
    (dispatch, ownProps) => ({
        updateSettings(settings) {
            dispatch({
                type: 'UPDATE_WIDGET_SETTINGS',
                id: ownProps.id,
                settings
            })
        }
    })
)(UserActivityWidgetSettings);


