import "babel-polyfill";
import React from 'react';
import { connect } from 'react-redux'
import UserActivityWidgetSettings from './UserActivityWidgetSettings';
import * as selectors from './selectors';

class UserActivityMenu extends React.Component {
    constructor(props) {
        super(props);
        this.onBlur = this.onBlur.bind(this);
        this.onEdit = this.onEdit.bind(this);
        this.onDelete = this.onDelete.bind(this);
    }
    render() {
        return <div className="uaw-menu" ref={m => {this.menu = m;}}>
            <button className="uaw-menu-item" ref={b => {this.editWidgetButton = b;}} onClick={this.onEdit}>Edit Widget</button>
            <button className="uaw-menu-item" onClick={this.onDelete}>Delete Widget</button>
        </div>
    }

    componentDidMount() { 
        this.menu.addEventListener("blur", this.onBlur, true);       
        this.editWidgetButton.focus();
    }

    onBlur() {
        setTimeout(()=>{
            if (this.menu && this.menu.querySelectorAll(':focus').length == 0) {                
                this.props.onHideMenu && this.props.onHideMenu();
            }
        }, 10);        
    }

    onEdit() {
        this.props.onEdit && this.props.onEdit();
    }

    onDelete() {
        this.props.onDelete && this.props.onDelete();
    }
}    

class UserActivityWidget extends React.Component {
    constructor (props) {
        super(props);
        this.state = {
            menuOpen: false,
            settingsOpen: false
        };
        this.onheaderclick = this.onheaderclick.bind(this);        
    }

    onheaderclick(e) {        
        if (e.target !== this.widgetDiv && e.target !== this.widgetHeader) {
            return;
        }
        let rect = this.widgetDiv.getBoundingClientRect();    
        let data = {
            x: e.pageX,
            y: e.pageY,
            dx: e.pageX - rect.left - window.pageXOffset,
            dy: e.pageY - rect.top - window.pageYOffset
        }                
        this.props.onHeaderClick && this.props.onHeaderClick(data);
        return false;
    }    

    showMenu() {
        this.setState({menuOpen: true})
    }

    hideMenu() {
        this.setState({menuOpen: false})
    }

    showSettings() {
        this.setState({settingsOpen: true});
    }    

    tableRow(x, idx) {
        let percent = Math.round(x.weekly);
        return <tr key={idx} className="uaw-row">
            <td className="uaw-avatar"></td>
            <td className="uaw-name">{x.name} {x.lastname}</td>
            <td className="uaw-progressbar"><div className="uaw-progressbar-bg">
                    <div className="uaw-progressbar-fg" style={{width: percent + "%"}}></div>
                </div></td>
            <td className="uaw-percent">{percent}%</td>
        </tr>
    }   

    menu() {
        if (this.state.menuOpen) {
            return <div className="uaw-menu-wrapper">            
                <UserActivityMenu
                    onHideMenu={() => this.hideMenu()}
                    onEdit={() => this.showSettings()}
                    onDelete={() => this.props.onDelete()}
                />
            </div>
        }
        return false;
    }    

    settings() {
        if (this.state.settingsOpen) {
            return <UserActivityWidgetSettings
                id={this.props.id}
                onCancel={()=>this.setState({settingsOpen: false})}
                onSave={()=>this.setState({settingsOpen: false})}
            />
        }
        return false;
    }

    componentDidMount() {
        this.widgetDiv.onmousedown = this.onheaderclick;        
    }

    componentWillUnmount() {

    }   

    render() {
        return <div className="userActivityWidget" 
                    ref={x => {this.widgetDiv = x;}} 
                    style={this.props.style || {}}
            >
            <div className="widgetHeader" ref={x=>{this.widgetHeader = x;}}>
                <div className="widgetHeaderTitle">Users activity</div>
                <div className="widgetHeaderRight">
                    <div className="timeSelector">Weekly</div>
                    <div className="arrowDown"></div>
                    <button className="menuButton" onClick={()=>this.showMenu()}></button>                    
                </div>
            </div>
            {this.menu()}
            {this.settings()}
            <div className="widgetBody">
                <table>                
                    <tbody>
                        {this.props.data.map(this.tableRow.bind(this))}
                    </tbody>
                </table>
            </div>
            <div className="widgetFooter"></div>
        </div>
    }
};

export default connect(
    (state, ownProps) => ({
        data: selectors.userActivity(state, ownProps)
    })
) (UserActivityWidget);


